@use 'base/mixins' as m;
@use 'base/variables' as v;

.filtros{
    background-color: lighten($color: v.$blanco, $amount: 20);
    margin-top: 2rem;
    border-radius: 1rem;
    padding: 2rem;
    .filtros-inputs{
        @include m.tablet{
            display: flex;
            align-items: center;
            gap: 2rem;
        }
    }
    h2{
        color: v.$indigo;
        font-size: 2.8rem;
        @include m.tablet{
            margin: 0;
        }
    }

    .campo{
        margin-bottom: 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 1rem;

        @include m.tablet {
            margin-bottom: 0;
        }
    }

    input[type="radio"]{
        accent-color: v.$indigo;
        width: 2rem;
        height: 2rem;
    }
}